.homeMain
  width: 100%
  //height: 100%
  display: flex
  flex-direction: column
  //background: linear-gradient(0deg, #111F4C 0%, #072E92 100%)
  .wrapper
    padding: 0 10rem 5rem 10rem
  &__info
    width: 100%
    padding-left: 10rem
    display: flex
    //justify-content: space-between
    align-items: center
    //gap: 5rem
    .info
      //align-self: flex-start
      color: white
      display: flex
      flex-direction: column
      gap: 3rem
      &__title
        font-size: 6.4rem
        //color: white
      &__text
        font-size: 2.4rem
        //color: white
  &__programs
    //margin: 0 10rem 5rem 10rem
    padding: 2.9rem 7rem 2.9rem 6.8rem
    background: #FFFFFF1F
    display: flex
    align-items: center
    gap: 3rem
    border-radius: 1.5rem
    overflow: hidden
    .programsInfo
      min-width: 34rem
      display: flex
      flex-direction: column
      gap: 2rem
      color: white
      &__title
        font-size: 3.6rem
      &__text
        font-size: 2rem
        color: #FFFFFFBD
    .items
      display: flex
      gap: 3rem
      &__inner
        //min-width: 25rem
        cursor: pointer
        padding: 3rem 2.1rem 3rem 2.1rem
        display: flex
        flex-direction: column
        gap: 1.4rem
        border-radius: 1.361rem
        transition: .3s
        &:hover
          box-shadow: 0 0 2rem 0 rgba(0, 0, 0, 0.25)
        &.active
          background: #6100FF
      &__image
        width: 5.416rem
      &__title
        color: white
        font-size: 2.4rem
      &__more
        color: white
        font-size: 1.6rem

@media (max-width: 1440px)
  .homeMain
    &__programs
      .items
        &__inner
          min-width: 24.5rem
        &__title
          //fz
    .info
      &__title
        font-size: 4.8rem
      &__text
        font-size: 1.8rem
    .image
      width: 63rem
      height: 50rem
      display: flex
      align-items: flex-end
      img
        width: 100%

@media (max-width: 1024px)
  .homeMain
    height: 80%
    &__programs
      .items
        &__inner
          min-width: 21.5rem
        &__title
          font-size: 2rem
    &__info
      padding-left: 5rem
    .wrapper
      padding: 0 5rem 5rem 5rem
    .info
      &__title
        font-size: 3.8rem
      &__text
        font-size: 1.4rem
    .image
      width: 48rem
      height: 35rem
    .programsInfo
      &__title
        font-size: 3rem
      &__text
        font-size: 1.8rem

@media (max-width: 768px)
  .homeMain
    &__info
      flex-direction: column
      .info
        align-self: flex-start
    &__programs
      padding: 2.2rem 5.2rem 2.2rem 5.2rem
      gap: 2rem
      .programsInfo
        min-width: 27rem
        &__title
          font-size: 2.7rem
        &__text
          font-size: 1.6rem
      .items
        &__inner
          min-width: 20rem
        &__image
          width: 4.1rem
        &__title
          font-size: 1.85rem

@media (max-width: 430px)
  .homeMain

    &__info
      padding-left: 1.5rem
      align-items: normal
      .info
        &__title
          font-size: 2.4rem
        &__text
          font-size: 1.2rem
    .image
      width: 40rem
      height: 30rem
    &__programs
      flex-direction: column
      .programsInfo
        padding-bottom: 2rem
        border-bottom: 1px solid white

